.container {
  background-color: var(--color-1);
  padding-left: 40px;
  padding-right: 40px;
  padding-top: 40px;
  padding-bottom: 60px;
  font-size: 18px;
}
.title {
  font-size: 35px;
  font-weight: 600;
  margin-bottom: 20px;
  color: #fff;
}
.links_wrapper {
  display: flex;
  gap: 20px 50px;
  flex-wrap: wrap;
}
.links_block {
  display: flex;
  gap: 40px;
}

.link_title {
  font-weight: 600;
  color: #fff;
}
.link_text {
  color: var(--color-3);
  text-decoration: underline;
  font-weight: 600;
}

@media (max-width: 992px) {
  .container {
    padding-left: 20px;
    padding-right: 20px;
  }
}
@media (max-width: 768px) {
  .title {
    font-size: 25px;
  }
  .links_wrapper {
    display: block;
  }
  .links_block {
    display: block;
  }
}
