.container {
  background-color: var(--color-2);
  padding-left: 40px;
  padding-right: 40px;
  padding-top: 45px;
  padding-bottom: 45px;
  font-size: 18px;
  scroll-margin-top: 60px;
}
.content {
  background-color: #fff;
  box-shadow: 0 0px 16px rgba(0, 0, 0, 0.18);
  border-radius: 10px;
  padding: 30px 15px;
}
.title {
  font-size: 35px;
  font-weight: 600;
  margin-bottom: 15px;
}
/* ---------------------------------------- */

.bonus {
  max-width: 602px;
  border-radius: 8px;
  margin-bottom: 20px;
  font-size: 16px;
}

.price_info {
  margin-bottom: 35px;
}

.price_info h3 {
  margin-bottom: 12px;
  font-size: 22px;
  max-width: 450px;
}

.price_info span {
  color: var(--color-4);
  font-weight: bold;
}

.price_info ul {
  list-style: none;
  padding: 0;
  font-size: 16px;
  margin: 0;
}

.price_info li {
  position: relative;
  padding-left: 28px;
  margin-bottom: 10px;
  font-size: 16px;
  line-height: 1.4;
}

.price_info li::before {
  content: '✅';
  position: absolute;
  left: 0;
  top: 0;
  font-size: 18px;
  line-height: 1.4;
}

.btn_join {
  display: inline-block;
  margin-top: 24px;
  background-color: var(--color-4);
  color: white;
  text-decoration: none;
  padding: 14px 24px;
  font-size: 14px;
  transition: background 0.3s ease;

  padding: 14px;
  text-transform: uppercase;
  border-radius: 10px;
  display: inline-block;

  letter-spacing: 2px;
}

.btn_join:hover {
  opacity: 0.8;
}

/* ---------------------------------------- */

@media (max-width: 992px) {
  .container {
    padding-left: 20px;
    padding-right: 20px;
  }
}
@media (max-width: 768px) {
  .title {
    font-size: 25px;
  }
  .price_info h3 {
    margin-bottom: 12px;
    font-size: 18px;
  }

  .btn_join {
    width: 100%;
    max-width: 350px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
}
