.container {
  background-color: var(--color-2);
  padding-left: 40px;
  padding-right: 40px;
  padding-top: 40px;
  padding-bottom: 40px;
  font-size: 22px;
  scroll-margin-top: 60px;
}
.title {
  font-size: 35px;
  font-weight: 600;
  margin-bottom: 10px;
}

.list {
  list-style: none;
  padding: 0;
  font-size: 16px;
  margin: 0;
}
.list li {
  position: relative;
  padding-left: 28px;
  margin-bottom: 10px;
  font-size: 16px;
  line-height: 1.4;
}

.list li::before {
  content: '✅';
  position: absolute;
  left: 0;
  top: 0;
  font-size: 18px;
  line-height: 1.4;
}

@media (max-width: 992px) {
  .container {
    padding-left: 20px;
    padding-right: 20px;
  }
}
@media (max-width: 768px) {
  .container {
    font-size: 16px;
  }
  .title {
    font-size: 25px;
  }
}
