.container {
  box-shadow: 0 0px 5px rgb(0, 0, 0);
  background-color: #000000c0;
  backdrop-filter: blur(7px);
  -webkit-backdrop-filter: blur(7px);
  position: fixed;
  top: 0px;
  left: 50%;
  transform: translate(-50%);
  width: 100%;
  height: 100%;
  max-width: 1100px;
  z-index: 110;
  pointer-events: none;
  opacity: 0;

  transition: opacity 0.3s;
}
.container.menuOpen {
  opacity: 1;
  pointer-events: auto;
}
.content_wrapper {
  background-color: var(--color-1);
  height: 100%;
  width: 350px;
  max-width: 100%;
  position: absolute;
  right: 0;
  top: -110%;

  padding: 70px 5px 50px 5px;
  overflow-y: auto;
  color: #fff;
  transition: top 0.3s;
}
.menuOpen .content_wrapper {
  top: 0;
}
.close_btn {
  position: absolute;
  cursor: pointer;
  top: 15px;
  right: 15px;
}

.list_wrapper {
  padding: 10px;
  margin: 0;
  list-style: none;
  font-size: 18px;
}

.list_wrapper li {
  margin-bottom: 7px;
}
.list_wrapper a {
  display: inline-block;
  padding: 7px;
}

@media (any-hover: hover) {
  .list_wrapper a:hover {
    text-decoration: underline;
  }
}
