.container {
  background-color: var(--color-2);
  padding-left: 40px;
  padding-right: 40px;
  padding-top: 40px;
  padding-bottom: 50px;
  scroll-margin-top: 60px;
}
.title {
  font-size: 35px;
  font-weight: 600;
  margin-bottom: 10px;
}

/* --------------------------------------------------------------- */
.accordion {
  font-family: sans-serif;
}

.accordion_item {
  border-bottom: 1px solid #ddd;
}

.accordion_header {
  width: 100%;
  text-align: left;
  padding-left: 1rem;
  padding-right: 1rem;
  padding-top: 0.7rem;
  padding-bottom: 0.7rem;
  background: var(--color-7);
  /* background: var(--color-1); */
  border-left: 1px solid var(--color-1);
  border-right: 1px solid var(--color-1);
  border: none;
  outline: none;
  font-size: 1rem;
  cursor: pointer;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.accordion_content {
  overflow: hidden;
  max-height: 0;
  transition: max-height 0.3s ease;
  /* border-left: 1px solid #ddd;
  border-right: 1px solid #ddd; */
  font-size: 1rem;
}

.content_inner {
  padding: 0.5rem;
  padding-bottom: 1rem;
  background: #fff;
}

.arrow {
  font-size: 1.5rem;
  transition: transform 0.3s ease;
  margin-left: 10px;
}

.accordion_item.open .arrow {
  transform: rotate(180deg);
}

/* --------------------------------------------------------------- */
.video_container {
  position: relative;
  width: 100%;
  height: 500px;
  max-height: 80vh;
  aspect-ratio: 16 / 9;
  background: var(--color-6);
  /* background-color: var(--color-6); */

  /* background-image: url('../../assets/img/pattern.png'); */
  border-radius: 12px;
  overflow: hidden;
}

.video_container video {
  margin: 0 auto;
  max-width: 100%;
  height: 100%;
  object-fit: contain;
  display: block;
}
/* --------------------------------------------------------------- */

.link_text {
  color: var(--color-1);
  font-weight: 600;
  text-decoration: underline;
}
.img {
  margin-bottom: 20px;
}

@media (max-width: 992px) {
  .container {
    padding-left: 20px;
    padding-right: 20px;
  }
}
@media (max-width: 768px) {
  .container {
    font-size: 16px;
  }
  .title {
    font-size: 25px;
  }
}
